 <template>
  <section id="table">
    <div class="container">
      <h2>MAKE A RESERVATION</h2>
      <p class="subtitle">
        Praesent blandit mollis dui a imperdiet. Vestibulum eu vehicula ante.
        Curabitur lacinia ipsum et porta ultrices. Morbi eu nunc convallis,
        ullamcorper ipsum nec
      </p>
      <form v-on:submit.prevent="sendMessage">
        <input v-model="name" id="name" placeholder="Name" />
        <input v-model="email" id="email" placeholder="Email" />
        <input v-model="phone" id="phone" placeholder="Phone" />
        <input v-model="date" id="date" type="date" />
        <input v-model="time" id="timeX" type="time" />
        <textarea
          v-model="message"
          id="text-comment"
          placeholder="Message"
          maxlength="160"
        ></textarea>

        <button>SEND US</button>
      </form>
    </div>
  </section>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import VueToast from "vue-toast-notification";
import "vue-toast-notification/dist/theme-sugar.css";

Vue.use(VueToast);
Vue.use(VueAxios, axios);

export default {
  data() {
    return {
      name: "",
      email: "",
      phone: "",
      date: "",
      time: "",
      message: "",
    };
  },
  methods: {
    sendMessage() {
      console.log(
        this.name +
          ", " +
          this.email +
          ", " +
          this.phone +
          ", " +
          this.date +
          ", " +
          this.time +
          ", " +
          this.message
      );
      let valid = true;
      if (
        this.name == "" ||
        this.email == "" ||
        this.phone == "" ||
        this.date == "" ||
        this.time == "" ||
        this.message == ""
      ) {
        valid = false;
      }
      if (this.time < "09:00" || this.time > "20:00") {
        valid = false;
        Vue.$toast.error("Correct time from 9:00 to 20:00", {
          position: "top",
        });
      }
      if (valid) {
        const BOT_KEY = "1488549434:AAHSlU7W6fxg_pH5-HC-bW6fr90VgRjQVaA";
        const CHAT_ID = "-1001361932841";
        let msg_text = encodeURI(
          "<b>Name: </b>" +
            this.name +
            "\n<b>Email: </b>" +
            this.email +
            "\n<b>Phone: </b>" +
            this.phone +
            "\n<b>Date: </b>" +
            this.date +
            "\n<b>Time: </b>" +
            this.time +
            "\n<b>Message:</b> <i>" +
            this.message +
            "</i>"
        );
        axios
          .get(
            "https://api.telegram.org/bot" +
              BOT_KEY +
              "/sendMessage?chat_id=" +
              CHAT_ID +
              "&parse_mode=html&text=" +
              msg_text
          )
          .then((resp) => {
            if (resp.data.ok) {
              this.name = "";
              this.email = "";
              this.phone = "";
              this.date = "";
              this.time = "";
              this.message = "";
              Vue.$toast.success("Message send. We contact you soon.", {
                position: "top",
              });
            } else {
              Vue.$toast.error("Some error occures", { position: "top" });
            }
          })
          .catch((err) => {
            console.log(err);
            Vue.$toast.error(err);
          });
      } else {
        Vue.$toast.warning("Fill all fields", { position: "top" });
      }
    },
  },
};
</script>